import { getJSErrorList } from "@/api/modules/web/jserror";
import { Table, message } from "antd";
import { ColumnsType } from "antd/es/table";
import { useEffect, useState } from "react";
import { useSearchParams } from "react-router-dom";

interface DataType {
    key: React.Key;
    name: string;
    msg: string;
    stack: string;
    createdAt: string;
}

type JSErrorListProps = {
    onClick: (e: string) => void;
};

const JSErrorList: React.FC<JSErrorListProps> = (props: JSErrorListProps) => {
    const [pageNum, setPageNum] = useState(1);
    const [pageSize, setPageSize] = useState(10);
    const [errorTotal, setErrorTotal] = useState(0);
    const [errorList, setErrorList] = useState<DataType[]>([]);
    const [selectedRowKeys, setSelectedRowKeys] = useState<React.Key[]>([]);
    const [searchParams] = useSearchParams();
    const [messageApi, contextHolder] = message.useMessage();

    const columns: ColumnsType<DataType> = [
        {
            title: '错误名',
            dataIndex: 'name',
            width: 50,
        },
        {
            title: '错误信息',
            dataIndex: 'msg',
            width: 100,
        },
        {
            title: '错误内容',
            dataIndex: 'stack',
            width: 200,
            render: (text, record) => {
                return <div onClick={e => props.onClick(record.key as string)}>
                    <span style={{ display: 'inline', color:'#3370ff',fontSize:'16px'}}>{record.name}</span>&nbsp;&nbsp;&nbsp;
                    <span style={{ width: '200px', wordBreak: 'break-all' }}>{text}</span>
                </div>
            },
        },
        {
            title: '上报时间',
            dataIndex: 'createdAt',
            width: 100,
        },
    ];

    const onSelectChange = (newSelectedRowKeys: React.Key[]) => {
        setSelectedRowKeys(newSelectedRowKeys);
    };

    const rowSelection = {
        selectedRowKeys,
        onChange: onSelectChange,
    };

    const initJSErrorList = async () => {
        const appId = searchParams.get('appId');
        if (!appId) {
            messageApi.open({
                type: 'error',
                content: 'appId不能为空!',
            });
            return;
        }
        const result = await getJSErrorList({ pageNum, pageSize, appId });
        if (result.data) {
            setErrorTotal(result.data.total);
            const data: DataType[] = [];
            result.data.data.forEach(item => {
                data.push({
                    key: item.errorId,
                    name: item.name,
                    msg: item.msg,
                    stack: item.stack,
                    createdAt: item.createdAt,
                })
            });
            setErrorList(data);
        }
    };

    useEffect(() => {
        initJSErrorList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    useEffect(() => {
        initJSErrorList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [pageNum, pageSize])

    const pagination = {
        showSizeChanger: true,
        onChange: (page: number, pageSize: number) => {
            setPageNum(page);
            setPageSize(pageSize);
        },
        defaultCurrent: pageNum,
        total: errorTotal
    }

    return (<div>{contextHolder}<Table style={{ marginTop: '12px' }} rowSelection={rowSelection} pagination={pagination} columns={columns} dataSource={errorList} /></div>);
}

export default JSErrorList;