import FilterHeader from "@/components/FilterHeader";
import { Card, Collapse, CollapseProps, Divider, Drawer, Space, Typography } from "antd";
import { useState } from "react";
import JSErrorList from "./jsErrorList";
import { ClockCircleOutlined, CloseOutlined } from "@ant-design/icons";
import { getJSErrorInfo } from "@/api/modules/web/jserror";
import { ResJSError } from "@/api/interface/web/jserror";
import Paragraph from "antd/es/typography/Paragraph";
import Title from "antd/es/typography/Title";
import Table, { ColumnsType } from "antd/es/table";
import { getBreadcrumbList } from "@/api/modules/web/breadcrumb";

interface BreadcrumbDataType {
    type: string;
    category: string;
    message: string;
    data: string;
    timestamp: string;
}

const Dashboard: React.FC = () => {
    const [jsErrorInfo, setJsErrorInfo] = useState<ResJSError>();
    const [pageNum, setPageNum] = useState(1);
    const [pageSize, setPageSize] = useState(10);
    const [breadcrumbTotal, setBreadcrumbTotal] = useState(0);
    const [open, setOpen] = useState(false);
    const [breadcrumbList, setBreadcrumbList] = useState<BreadcrumbDataType[]>([]);

    const items: CollapseProps['items'] = [
        {
            key: '1',
            label: '堆栈明细',
            children: <p>{jsErrorInfo && jsErrorInfo.stack}</p>,
        },
    ];

    const breadcrumbColumns: ColumnsType<BreadcrumbDataType> = [
        {
            title: '类型',
            dataIndex: 'type',
            width: 50,
        },
        {
            title: '策略',
            dataIndex: 'category',
            width: 100,
        },
        {
            title: '信息',
            dataIndex: 'message',
            width: 200,
            render: (text, record) => {
                return <div>
                    <span style={{ width: '200px', wordBreak: 'break-all' }}>{text}</span>
                </div>
            },
        },
        {
            title: '附加信息',
            dataIndex: 'data',
            width: 100,
        },
        {
            title: '上报时间',
            dataIndex: 'timestamp',
            width: 100,
        },
    ];

    const initBreadcrumbList = async (errorId: string) => {
        const result = await getBreadcrumbList({ pageNum, pageSize, errorId });
        if (result.data) {
            setBreadcrumbTotal(result.data.total);
            const data: BreadcrumbDataType[] = [];
            result.data.data.forEach((item: { type: any; category: any; message: any; data: any; timestamp: any; }) => {
                data.push({
                    type: item.type,
                    category: item.category,
                    message: item.message,
                    data: item.data,
                    timestamp: item.timestamp
                })
            });
            setBreadcrumbList(data);
        }
    }

    const onClose = () => {
        setOpen(false);
    };

    const getJSError = async (id: string) => {
        const res = await getJSErrorInfo(id);
        res.data && setJsErrorInfo(res.data);
        initBreadcrumbList(id);
    }

    const showDrawer = (key: string) => {
        getJSError(key);
        setOpen(true);
    };

    const pagination = {
        showSizeChanger: true,
        onChange: (page: number, pageSize: number) => {
            setPageNum(page);
            setPageSize(pageSize);
        },
        defaultCurrent: pageNum,
        total: breadcrumbTotal
    }

    return (<div>
        <FilterHeader></FilterHeader>
        <Card title="错误列表" style={{ marginTop: '16px' }}>
            <JSErrorList onClick={showDrawer}></JSErrorList>
        </Card>
        <Drawer
            title="错误详情"
            placement='right'
            closable={false}
            width='80%'
            onClose={onClose}
            open={open}
            extra={
                <Space>
                    <CloseOutlined onClick={onClose} />
                </Space>
            }
        >
            <div>
                <Typography>
                    <Title level={4}>{jsErrorInfo?.name}</Title>
                    <Paragraph>
                        {jsErrorInfo?.msg}
                    </Paragraph>
                    <Paragraph>
                        <ClockCircleOutlined />&nbsp;&nbsp;{jsErrorInfo?.createdAt}
                    </Paragraph>
                </Typography>
                <Divider />
                <div style={{ width: '60%' }}>
                    <Table style={{ marginTop: '12px' }} size="small" pagination={pagination} columns={breadcrumbColumns} dataSource={breadcrumbList} />
                    <Typography>
                        <Title level={5}>Js错误堆栈</Title>
                        <Paragraph>
                            {jsErrorInfo?.msg}
                        </Paragraph>
                    </Typography>
                    <Collapse bordered={false} items={items} defaultActiveKey={['1']} />
                </div>
            </div>
        </Drawer>
    </div>);
}

export default Dashboard;